<template>
    <div style="position: relative;">
        <div style="height: 600px; width:100%;" id="maps">
            <FilterData 
                :keyData=0
                @filterData="filterData"
                @resetData="resetData"
            />
            <Graphics 
                :keyData=0
                @openGraphics="openGraphics"
            />
            <Maps
                :dataMaps="this.dataMaps"
                :keyData="this.keyMaps"
                :centerCoord = "this.centerCoord"
                :detailInformation = 'this.detailInformation'
                :eye= 'this.isEye'
                :resetData="this.resetData"
                @infoData="infoData"
            />
        </div>
        <div style="width:100%;">
            <Information 
                :detailInformation="this.detailInformation"
            />
            <Table
                :dataTable="this.dataTable"
                @spesificData="passData"
            />
        </div>

    </div>
</template>

<script>
import Maps from './Maps.vue'
import data from '../data/pozzi3.json'
import Table from './Table.vue'
import Information from './Information.vue'
import Graphics from './Graphics.vue';
import FilterData from './FilterData.vue';


export default {
    components: {
        Maps,
        Table,
        Information,
        Graphics,
        FilterData
    },
    data () {
        return{
            dataMaps : data,
            centerCoord : [],
            detailInformation: [],
            keyMaps : 0,
            keyDetail : 0,
            isEye : false,
            dataTable: data,
            reset : false,
        }
    },
    methods: {
        openGraphics(val){
          val
          document.getElementById("mySidebarLeft").style.display = "unset";
        },
        passData(val, eye){
            this.isEye = eye
            this.detailInformation = []
            this.keyMaps = val
            for(var i=0; i< this.dataMaps.length; i++){
                if(this.dataMaps[i]['key'] == val){
                    this.centerCoord = this.dataMaps[i]['latLan']
                    this.detailInformation.push(this.dataMaps[i])
                    break
                }
            }
            if(eye){
                this.openNav()
            }
            else{ 
                this.closeNav()
            }
        },
        openNav(){
            document.getElementById("mySidebar").style.display = "unset";
        },
        closeNav(){
            document.getElementById("mySidebar").style.display = "none";
        },
        infoData(val, eye){
            this.isEye = eye
            this.detailInformation = []
            this.keyMaps = val
            for(var i=0; i< this.dataMaps.length; i++){
                if(this.dataMaps[i]['key'] == val){
                    this.detailInformation.push(this.dataMaps[i])
                    break
                }
            }
            if(eye){    
                this.openNav()
            }
        },
        filterData(dataFilter){
            this.dataMaps = data
            this.dataMaps = this.dataMaps.filter(function(item){
                var checkRegione, checkTipo,checkEsito, checkScopo, checkUso, checkStato  = false;
                if(item.regione == dataFilter.regione || dataFilter.regione == "" || dataFilter.regione == "ALL" ){
                    checkRegione = true
                }
                if(item.tipo == dataFilter.tipo || dataFilter.tipo == "" || dataFilter.tipo == "ALL" ){
                    checkTipo = true
                }
                if(item.esito == dataFilter.esito || dataFilter.esito == "" || dataFilter.esito == "ALL" ){
                    checkEsito = true
                }
                if(item.scopo == dataFilter.scopo || dataFilter.scopo == "" || dataFilter.scopo == "ALL" ){
                    checkScopo = true
                }
                if(item.uso == dataFilter.uso || dataFilter.uso == "" || dataFilter.uso == "ALL" ){
                    checkUso = true
                }
                if(item.stato == dataFilter.stato || dataFilter.stato == "" || dataFilter.stato == "ALL" ){
                    checkStato = true
                }
                if(checkRegione && checkTipo && checkEsito && checkScopo && checkUso && checkStato){
                    return true;
                }
                else{
                    return false;
                }
            })
            this.dataTable = this.dataMaps
        },
        resetData(){
            this.dataMaps = data
            this.dataTable = data
            this.resetData = true;
        }
    },
    created: function(){
    },
    computed:{

    },
    mounted: function(){

    },
    watch:{
        centerCoord: function(){
            this.centerCoord
        },
        detailInformation : function(){
            this.detailInformation
        },
        keyMaps: function(){
            this.keyMaps
        },
        openNavGraphics: function(){
            this.openNavGraphics
        },
        dataMaps: function(newValue,oldValue){
            this.dataMaps = newValue
            oldValue
        },
        dataTable: function(newValue,oldValue){
            this.dataTable  = newValue
            oldValue
        }
    }
}
</script>