<template>
    <div>
        <div class="sidebarInformationLeft" id="mySidebarLeft" >
            <div>
            <div class="tab" style="overflow:hidden;" >
                <button class="tablinks" @click="openGraph($event, 'regione')">Regione</button>
                <button class="tablinks" @click="openGraph($event, 'timeseries')">Timeseries</button>
                <a href="javascript:void(0)" class="closebtn" @click="closeNav">&times;</a>
            </div>
            <div id="regione" class="tabcontents">
                <div class="row">
                        <div class="col-3" style="padding-top:50px;background-color: #363333; height: 553px;padding: 30px;color: white;">
                            <div>
                                <model-select :options="menus"
                                                v-model="regionSelected"
                                                placeholder="select regione">
                                </model-select>
                            </div>
                            <div>
                                <div class="number">
                                    <div class="row" style="padding-left:15px;padding-right:15px;">
                                        <div class="text-left infoNumberLeft"><h6>Total Data</h6></div>
                                        <div class="text-right infoNumberRight"><h4>{{ selectedData.total_data }}</h4></div>
                                    </div>
                                    <div><md-progress-bar md-mode="determinate" :md-value="selectedData.total_data/totalData*100"></md-progress-bar></div>
                                </div>
                                <div class="number">
                                    <div class="row" style="padding-left:15px;padding-right:15px;">
                                        <div class="text-left infoNumberLeft"><h6>Maximum Depth</h6></div>
                                        <div class="text-right infoNumberRight"><h4>{{ selectedData.max_quota }}</h4></div>
                                    </div>
                                    <div><md-progress-bar class="md-accent" md-mode="determinate" :md-value="selectedData.max_quota/maxQuata*100"></md-progress-bar></div>
                                </div>
                                <div class="number">
                                    <div class="row" style="padding-left:15px;padding-right:15px;">
                                        <div class="text-left infoNumberLeft"><h6>Minimum Depth</h6></div>
                                        <div class="text-right infoNumberRight"><h4>{{ selectedData.min_quota }}</h4></div>
                                    </div>
                                    <div><md-progress-bar md-mode="determinate" :md-value="selectedData.min_quota/minQuota*100"></md-progress-bar></div>
                                </div>
                                <div class="number">
                                    <div class="row" style="padding-left:15px;padding-right:15px;">
                                        <div class="text-left infoNumberLeft"><h6>Largest Depth Of Borehole</h6></div>
                                        <div class="text-right infoNumberRight"><h4>{{ selectedData.max_prof }}</h4></div>
                                    </div>
                                    <div><md-progress-bar class="md-accent" md-mode="determinate" :md-value="selectedData.max_prof/maxProf*100"></md-progress-bar></div>
                                </div>
                                <div class="number">
                                    <div class="row" style="padding-left:15px;padding-right:15px;">
                                        <div class="text-left infoNumberLeft"><h6>Smallest Depth Of Borehole</h6></div>
                                        <div class="text-right infoNumberRight"><h4>{{ selectedData.min_prof }}</h4></div>
                                    </div>
                                    <div><md-progress-bar md-mode="determinate" :md-value="selectedData.min_prof/minProf*100"></md-progress-bar></div>
                                </div>
                            </div>
                        </div>
                        <div class="col-9" style="padding-top:30px;">
                            <div class="tab">
                                <button class="tablinks" @click="openGraphs($event, 'bar')">Position</button>
                                <button class="tablinks" @click="openGraphs($event, 'directed')">Depth</button>
                            </div>
                            <div style="padding-right:10px;margin-top: 70px;" id="bar" class="tabcontent">
                                <ApexCharts type="bar" height="350" :options="selectedData.bar.chartOptions" :series="selectedData.bar.series"></ApexCharts>
                            </div>
                            <div style="padding-right:10px;" id="directed" class="tabcontent">
                                    <div class="hello" ref="chartdiv" style="height:100%;" id="chartdiv">
                                    </div>
                                    <div>
                                    Nodes indicate the <b>total depth</b> <br/>
                                    <b>Max : </b> Maximum Depth | <b>Min : </b> Minimum Depth | <b>L : </b> Largest Depth Of Borehole | <b>S : </b> Smallest Depth Of Borehole

                                    </div>
                            </div>
                        </div>
                        </div>
                    </div>
                </div>
            <div>
            <div id="timeseries" class="tabcontents">
                <div id="wrapper">
                    <div id="chart-line1">
                        <ApexCharts type="line" height="350" :options="chartOptions" :series="series"></ApexCharts>
                    </div>
                    <div id="chart-line">
                        <ApexCharts type="area" height="160" :options="chartOptionsLine" :series="seriesLine"></ApexCharts>
                    </div>
                </div>
            </div>
            </div>
        </div>
    </div>
</template>

<script>
import ApexCharts from 'vue-apexcharts';
import data from '../data/statistics.json';
import timeseries_prof_max from '../data/timeseries_prof_max.json';
import timeseries_prof_min from '../data/timeseries_prof_min.json';
import timeseries_quota_max from '../data/timeseries_quota_max.json';
import timeseries_quota_min from '../data/timeseries_quota_min.json';
import depth from '../data/depth.json';
import { ModelSelect } from 'vue-search-select'
import * as am4core from "@amcharts/amcharts4/core";
import * as am4plugins_forceDirected from "@amcharts/amcharts4/plugins/forceDirected";

export default {
    props: ['keydata'],
    components: {
        ApexCharts,
        ModelSelect,

    },
    data () {
        return{
            statistics : data,
            regionSelected : '',
            totalData: 0,
            maxQuata: 0,
            minQuota: 0,
            maxProf: 0,
            minProf: 0,
            selectedData : {
                            total_data: 0,
                            max_quota: 0,
                            min_quota: 0,
                            max_prof: 0,
                            min_prof: 0,
                            bar:
                                {
                                series: [],
                                chartOptions: {
                                        chart: {
                                                type: 'bar',
                                                height: 350,
                                                stacked: true,
                                                toolbar: {
                                                        show: true
                                                        },
                                                zoom: {
                                                        enabled: true
                                                    }
                                        },
                                        responsive: [{
                                            reakpoint: 480,
                                            options: {
                                                    legend: {
                                                    position: 'bottom',
                                                    offsetX: -10,
                                                    offsetY: 0
                                                }
                                            }
                                            }
                                        ],
                                        plotOptions: {
                                        bar: {
                                                horizontal: false,
                                            },
                                        },
                                        xaxis: {
                                                type: 'string',
                                                categories: [],
                                            },
                                        legend: {
                                                position: 'right',
                                                offsetY: 40
                                            },
                                        fill: {
                                                opacity: 1
                                        }
                                    }
                                }
                        },
            menus: [],
            series: [
                { 
                    name: "Maximum Depth",
                    data: timeseries_quota_max
                },
                { 
                    name: "Minimum Depth",
                    data: timeseries_quota_min
                },
                
                { 
                    name: "Largest Depth Of Borehole",
                    data: timeseries_prof_max
                },
                
                { 
                    name: "Smalest Depth Of Borehole",
                    data: timeseries_prof_min
                },
                
                ],
            chartOptions: {
                chart: {
                            id: 'chart1',
                            group: 'borehole',
                            type: 'line',
                            height: 230,
                            toolbar: {
                                autoSelected: 'pan',
                                show: false
                            }
                        },
                annotations: {
                            yaxis: [{
                                    y: 0,
                                    borderColor: '#ff5252',
                                    label :{
                                        minWidth : 5,
                                    }
                                }],
                },
                colors: ['rgb(0, 143, 251)', 'rgb(0, 227, 150)', '#E6550D', 'rgb(254, 176, 25)'],
                stroke: {
                        width: 3
                        },
                dataLabels: {
                            enabled: false
                            },
                fill: {
                        opacity: 1,
                        },
                markers: {
                        size: 0
                        },
                xaxis: {
                        type: 'category',
                        label: {
                            datetimeFormatter: {
                                year: 'yyyy',
                            },
                        }
                },
                yaxis: {
                    labels: {
                            minWidth: 5
                        }
                }
            },
            seriesLine:[
                { 
                    name: "Maximum Depth",
                    data: timeseries_quota_max
                },
                { 
                    name: "MInimum Depth",
                    data: timeseries_quota_min
                },
                { 
                    name: "Largest Depth Of Borehole",
                    data: timeseries_prof_max
                },
                { 
                    name: "Smalest Depth Of Borehole",
                    data: timeseries_prof_min
                },
                ],
            chartOptionsLine: {
                chart: {
                id: 'chart3',
                height: 130,
                type: 'area',
                brush:{
                    target: 'chart1',
                    enabled: true
                },
                selection: {
                    enabled: true,
                    xaxis: {
                    min: new Date('19 Jun 1926').getYear(),
                    max: new Date('14 Aug 1935').getYear()
                    }
                },
                },
                colors: ['rgb(0, 143, 251)', 'rgb(0, 227, 150)', '#E6550D', 'rgb(254, 176, 25)'],
                fill: {
                type: 'gradient',
                gradient: {
                    opacityFrom: 0.91,
                    opacityTo: 0.1,
                }
                },
                xaxis: {
                    type: 'category',
                    label:{
                            datetimeFormatter: {
                                year: 'yyyy',
                            },
                    },
                    tooltip: {
                        enabled: false
                    }
                },
                yaxis: {
                    tickAmount: 2,
                    labels: {
                            minWidth: 5
                        }
                }
            },


                    
        }   
    },
    methods: {
        openGraph(evt, graph) {
            if(graph == "directed"){
                this.directGraph(this.regionSelected)
            }
            else{
                am4core.disposeAllCharts();
            }
            var i, tabcontent, tablinks;
            tabcontent = document.getElementsByClassName("tabcontents");
            for (i = 0; i < tabcontent.length; i++) {
                tabcontent[i].style.display = "none";
            }
            tablinks = document.getElementsByClassName("tablinks");
            for (i = 0; i < tablinks.length; i++) {
                tablinks[i].className = tablinks[i].className.replace(" active", "");
            }
            document.getElementById(graph).style.display = "block";
            evt.currentTarget.className += " active";
        },
        openGraphs(evt, graph) {
            if(graph == "directed"){
                this.directGraph(this.regionSelected)
            }
            else{
                am4core.disposeAllCharts();
            }
            var i, tabcontent, tablinks;
            tabcontent = document.getElementsByClassName("tabcontent");
            for (i = 0; i < tabcontent.length; i++) {
                tabcontent[i].style.display = "none";
            }
            tablinks = document.getElementsByClassName("tablinks");
            for (i = 0; i < tablinks.length; i++) {
                tablinks[i].className = tablinks[i].className.replace(" active", "");
            }
            document.getElementById(graph).style.display = "block";
            evt.currentTarget.className += " active";
        },
        closeNav(){
            document.getElementById("mySidebarLeft").style.display = "none";
        },
        groupBy(){
            this.dataMaps['regione']
        },
        menu(){
            var res = []
            var terra = { name: 'TERRA', data:[]}
            var marre = { name: 'MARRE', data:[]}
            var acqua_interne = { name: 'ACQUA_INTERNE', data:[]}
            var non_indicato = { name: 'NON_INDICATO', data:[]}
            for(var i=0;i<this.statistics.length; i++){
                var a  = this.statistics[i].regione
                res.push({ value: a, text: a})
                if(this.statistics[i].regione == "All"){
                    this.totalData = this.statistics[i].total
                    this.maxQuata = this.statistics[i].max_quota
                    this.minQuota = this.statistics[i].min_quota
                    this.maxProf = this.statistics[i].max_prof
                    this.minProf = this.statistics[i].min_prof
                    this.selectedData.total_data = this.statistics[i].total
                    this.selectedData.max_quota = this.statistics[i].max_quota
                    this.selectedData.min_quota = this.statistics[i].min_quota
                    this.selectedData.max_prof = this.statistics[i].max_prof
                    this.selectedData.min_prof = this.statistics[i].min_prof
                    for(var j = 0; j<this.statistics[i].sub_regione.length; j++){
                        terra.data.push(this.statistics[i].sub_regione[j].terra)
                        marre.data.push(this.statistics[i].sub_regione[j].marre)
                        acqua_interne.data.push(this.statistics[i].sub_regione[j].acqua_interne)
                        non_indicato.data.push(this.statistics[i].sub_regione[j].non_indicato)
                        this.selectedData.bar.chartOptions.xaxis.categories.push(this.statistics[i].sub_regione[j].provincia)
                    }
                    this.selectedData.bar.series.push(terra)
                    this.selectedData.bar.series.push(marre)
                    this.selectedData.bar.series.push(acqua_interne)
                    this.selectedData.bar.series.push(non_indicato)
                    //this.selectedData.push(this.statistics[i])
                }

            }
            return res
        },
        selectData(val){
            this.selectedData = {
                            total_data: 0,
                            max_quota: 0,
                            min_quota: 0,
                            max_prof: 0,
                            min_prof: 0,
                            bar:
                                {
                                series: [],
                                chartOptions: {
                                        chart: {
                                                type: 'bar',
                                                height: 350,
                                                stacked: true,
                                                toolbar: {
                                                        show: true
                                                        },
                                                zoom: {
                                                        enabled: true
                                                    }
                                        },
                                        responsive: [{
                                            reakpoint: 480,
                                            options: {
                                                    legend: {
                                                    position: 'bottom',
                                                    offsetX: -10,
                                                    offsetY: 0
                                                }
                                            }
                                            }
                                        ],
                                        plotOptions: {
                                        bar: {
                                                horizontal: false,
                                            },
                                        },
                                        xaxis: {
                                                type: 'string',
                                                categories: [],
                                            },
                                        legend: {
                                                position: 'right',
                                                offsetY: 40
                                            },
                                        fill: {
                                                opacity: 1
                                        }
                                    }
                                }
                        }
            var terra = { name: 'TERRA', data:[]}
            var marre = { name: 'MARRE', data:[]}
            var acqua_interne = { name: 'ACQUA_INTERNE', data:[]}
            var non_indicato = { name: 'NON_INDICATO', data:[]}
            for(var i=0;i<this.statistics.length; i++){
                if(this.statistics[i].regione == val){
                    this.selectedData.total_data = this.statistics[i].total
                    this.selectedData.max_quota = this.statistics[i].max_quota
                    this.selectedData.min_quota = this.statistics[i].min_quota
                    this.selectedData.max_prof = this.statistics[i].max_prof
                    this.selectedData.min_prof = this.statistics[i].min_prof
                    for(var j = 0; j<this.statistics[i].sub_regione.length; j++){
                        terra.data.push(this.statistics[i].sub_regione[j].terra)
                        marre.data.push(this.statistics[i].sub_regione[j].marre)
                        acqua_interne.data.push(this.statistics[i].sub_regione[j].acqua_interne)
                        non_indicato.data.push(this.statistics[i].sub_regione[j].non_indicato)
                        this.selectedData.bar.chartOptions.xaxis.categories.push(this.statistics[i].sub_regione[j].provincia)
                    }
                    this.selectedData.bar.series.push(terra)
                    this.selectedData.bar.series.push(marre)
                    this.selectedData.bar.series.push(acqua_interne)
                    this.selectedData.bar.series.push(non_indicato)
                }
            }
            //start here
            this.directGraph(this.regionSelected)
        },

        directGraph(val = ""){
            if(this.regionSelected === "" || this.regionSelected === "All" || this.regionSelected === "undefined"){
                val = "ITALY"
            }
            var data = []
            for(var i=0;i < depth.length; i++){
                if(depth[i].name == val){
                    data.push(depth[i])
                }
            }
            let chart = am4core.create(this.$refs.chartdiv, am4plugins_forceDirected.ForceDirectedTree);
            // Create series
            let series = chart.series.push(new am4plugins_forceDirected.ForceDirectedSeries())
            series.data = data,
            // Set up data fields
            series.dataFields.value = "value";
            series.dataFields.name = "name";
            series.dataFields.children = "children";
            series.nodes.template.tooltipText = "{name}:{value}";
            series.nodes.template.fillOpacity = 1;
            // Add labels
            series.nodes.template.label.text = "{name}";
            series.nodes.template.label.fill = "black";
            series.fontSize = 10;
            series.minRadius = 15;
            series.maxRadius = 40;

            
        }
    },
    created: function(){
        
    },
    computed:{
    },
    mounted: function(){
        this.menus = this.menu()
        this.directGraph("ITALY")
    },
    watch:{
        regionSelected: function(){
            this.selectData(this.regionSelected)
        },
    }
}
</script>
<style>
.sidebarInformationLeft{
    position: absolute;
    background-color: white;
    float: Left;
    left: 0;
    height: 600px;
    transition: 0.5s;
    max-width: 100% !important;
    min-width: 888px;
    top: 0;
    z-index : 10;
    display: none;
    font-size:12px;
    width: 100%;
}

.sidebarInformationLeft .closebtn {
    position: absolute;
    top: 0;
    font-size: 36px;
    padding-right:15px;
    padding-top:15px;
    right:0;
}
.md-progress-bar {
}
.number{
    margin-top: 20px;
}
.infoNumberLeft{
    align-self: flex-end;
    width: 60%;
}

.infoNumberRight{
    align-self: flex-end;
    width: 40%;
}
/* Style the tab */
.tab {
    background-color: #f1f1f1;
}

/* Style the buttons inside the tab */
.tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
    background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
    background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
    display: none;
    padding: 6px 12px;
}
.tabcontents {
    display: none;
}

#chartdiv {
    width: 100%;
    height:420px !important;
    max-width:100%;
    margin-top: 50px;
}
</style>

